$(document).on('load', function() {
	// Set equal height to elems in footer carousel
	$(window).on('resize', function () {
		setEqualHeight($('.footer__menu-item'));
		//setEqualHeight($('.blog__item'));
	});

	setEqualHeight($('.footer__menu-item'));
	//setEqualHeight($('.blog__item'));

	function setEqualHeight(el) {
		if (window.matchMedia('(min-width: 1080px)').matches) {
			var tallestcolumn = 0;

			el.each(function() {
				var currentHeight = $(this).height();

				if (currentHeight > tallestcolumn) {
					tallestcolumn = currentHeight;
				}
			});

			el.height(tallestcolumn);
		} else {
			el.height('auto');
		}
	}
});
$(window).on('load', function() {
	if ($('.select').length){
		$('.select').select2({
			minimumResultsForSearch: Infinity
		});
	}

	$(document).on('click','.tabs-switch__item',function (){
		let index = $(this).index();
		$(this).addClass('active').siblings().removeClass('active');
		$(this).closest('.tabs-switch').next('.tabs-content').find('.tabs-content__item').eq(index).addClass('active').siblings().removeClass('active');
	});

	$(document).on('click','.accordion__title',function (){
		$(this).parent('.accordion').toggleClass('active').find('.accordion__text').slideToggle('300');
	});

	
	$('map').imageMapResize();
	

	const tokenSaleListItems = document.querySelectorAll('.token-sale__more-item-list li');

	if (tokenSaleListItems) {
		tokenSaleListItems.forEach(it => {
			const text = it.textContent;

			if (text.length > 22) {
				it.setAttribute('title', text);
				it.textContent = text.slice(0, 22).trim() + '...';
			}
		})
	}

	const text_modal_btn = document.querySelector('.text-modal-btn');
	const text_modal_first_screen = document.querySelector('.text-modal-first-screen');
	const text_modal_first_screen_close = document.querySelector('.text-modal-first-screen__close');
	const text_modal_first_screen_back = document.querySelector('.text-modal-first-screen__back');
	const body_ovh = document.querySelector('body');
	text_modal_btn.addEventListener("click", function(e) {
		text_modal_first_screen.classList.add('js-text-modal-active');
		body_ovh.style.cssText=`overflow: hidden`;
	});
	text_modal_first_screen_close.addEventListener("click", function(e) {
		text_modal_first_screen.classList.remove('js-text-modal-active');
		body_ovh.style.cssText=`overflow: visible`;
	});
	text_modal_first_screen_back.addEventListener("click", function(e) {
		text_modal_first_screen.classList.remove('js-text-modal-active');
		body_ovh.style.cssText=`overflow: visible`;
	});
})
$(document).ready(function() {
	var parent = $('.cookies'),
		btnAccept = parent.find('.cookies__button--accept'),
		btnClose = parent.find('.cookies__button--close');

	if (parent) {
		if (!Cookies.get('cookieAccepted')) {
			setTimeout(function () {
				parent.addClass('visible');
			}, 2000);

			btnAccept.click(function () {
				Cookies.set('cookieAccepted');
				parent.removeClass('visible');
			});

			btnClose.click(function () {
				parent.removeClass('visible');
			});
		}
	}
});
$(window).on('load', () => {
  let filterCarousel = null;
  let width = document.documentElement.clientWidth;
  let isInit = false;
  const breakpoint = 767;
  const carouselEl = document.querySelector('.services-section__carousel');

  if (carouselEl) {
    const servicesCarousel = new Swiper(carouselEl, {
      //loop: true,
      speed: 300,
      slidesPerView: 'auto',
      spaceBetween: 32,
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      }
    });

    const filtersBtns = document.querySelectorAll('.services-section__filter[data-target]');
    const slides = document.querySelectorAll('.services-section__card');
    
    if (width <= breakpoint) {
      initFilterCarousel();
      isInit = true;
    }

    if (filtersBtns && slides) {
      filterSlides();

      filtersBtns.forEach(it => {
        it.addEventListener('click', function(e) {
          filtersBtns.forEach(it => {
            it.classList.remove('active');
          });

          this.classList.add('active');
          filterSlides(this);
        })
      });
    }

    window.addEventListener('resize', () => {
      width = document.documentElement.clientWidth;
        
      if (width <= breakpoint && !isInit) {
        initFilterCarousel();
        isInit = true;
      } else if (width > breakpoint && isInit) {
        filterCarousel.destroy();
        isInit = false;
      }
    });

    function initFilterCarousel() {
      filterCarousel = new Swiper('.services-section__filters', {
        speed: 300,
        slidesPerView: 'auto',
        spaceBetween: 34
      });
    }

    function filterSlides(current) {
      const activeFilter = current || document.querySelector('.services-section__filter.active');
      const target = activeFilter.dataset.target;

      if (current) {
        carouselEl.classList.add('opacity');

        setTimeout(() => {
          carouselEl.classList.remove('opacity');
          for (slide of slides) {
            let categories = slide.dataset.category.split(',');
            categories = categories.map(it => {
              return it.trim()
            });
            slide.hidden = !categories.includes(target);
          }
          servicesCarousel.update();
        }, 550)
      
      } else {
        for (slide of slides) {
          let categories = slide.dataset.category.split(',');
          categories = categories.map(it => {
            return it.trim()
          });
          slide.hidden = !categories.includes(target);
        }
        servicesCarousel.update();
      }
    }
  }








});
window.addEventListener('load', () => {
  let width = document.documentElement.clientWidth;
  const blogCarouselOptions = {
    slidesPerView: 'auto',
    speed: 300,
    loop: true,
    navigation: {
      nextEl: '.slider-next-long'
    }
  }
  const elements = [
    {selector: '.__js_steps-carousel'},
    {selector: '.__js_rating-carousel'},
    {selector: '.__js-why-crypto-carousel'},
    {selector: '.__js_crypto-solution-carousel'},
    {selector: '.__js_crypto-line-carousel', breakpoint: 1079},
    {selector: '.__js_team-carousel'},
    {selector: '.__js_advisors-carousel'},
    {selector: '.__js_partners-carousel'},
    {selector: '.__js_token-sale-carousel'},
    {selector: '.__js_node-types-carousel'},
    {selector: '.__js_documents-carousel', breakpoint: 1079},
    {selector: '.__js_blog-carousel', breakpoint: 1079, options: blogCarouselOptions}
  ];

  elements.forEach(it => {
    const carouselEl = document.querySelector(it.selector);
    let isInit = false;
    let carousel = null;
    const options = it.options || {
      speed: 300,
      slidesPerView: 'auto',
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      },
    }

    if (carouselEl) {
      let isInit = false;
      let carousel = null;
      const breakpoint = it.breakpoint || 767;

      if (width <= breakpoint) {
        initCarousel(carouselEl);
        isInit = true;
      }

      window.addEventListener('resize', () => {
        width = document.documentElement.clientWidth;
          
        if (width <= breakpoint && !isInit) {
          initCarousel(carouselEl);
          isInit = true;
        } else if (width > breakpoint && isInit) {
          carousel.destroy();
          isInit = false;
        }
      });

      function initCarousel(el) {
        carousel = new Swiper(el, options);
      }
    }
  });

  
});
$(window).on('load', () => {
  let filterCarousel = null;
  let width = document.documentElement.clientWidth;
  let isInit = false;
  const breakpoint = 1079;
  const accordion = document.querySelector('.accordion');
  const activeClass = 'accordion__item--active';
  const filters = document.querySelectorAll('.faqs__filter[data-target]');
  const items = document.querySelectorAll('.accordion__item');

  if (accordion && filters.length) {

    accordion.addEventListener('click', e => {
      const target = e.target;
      
      if (target.closest('.accordion__item-header')) {
        if (target.parentElement.classList.contains(activeClass)) {
          target.parentElement.classList.remove(activeClass);
        } else {
          items.forEach(it => {
            it.classList.remove(activeClass)
          });
          target.parentElement.classList.add(activeClass);
        }
      }
    });

    if (filters) {
      if (width <= breakpoint) {
        initFilterCarousel();
        isInit = true;
      }
      
      filterAccordion();

      filters.forEach(it => {
        it.addEventListener('click', function() {
          filters.forEach(it => {
            it.classList.remove('active')
          });
          it.classList.add('active');
          filterAccordion(this);
        })
      });

      window.addEventListener('resize', () => {
        width = document.documentElement.clientWidth;

        if (width <= breakpoint && !isInit) {
          initFilterCarousel();
          isInit = true;
        } else if (width > breakpoint && isInit) {
          //console.log(filterCarousel)
          isInit = false;
          filterCarousel.destroy();
        }
      });

      function initFilterCarousel() {
        filterCarousel = new Swiper('.__js_faq-filters', {
          speed: 300,
          slidesPerView: 'auto'
        });
      }
    }
  }
  

  function filterAccordion(current) {
    const activeFilter = current || document.querySelector('.faqs__filter.active');
    const target = activeFilter ? activeFilter.dataset.target : null;

    if (current) {
      accordion.classList.add('opacity');
      
      accordion.addEventListener('transitionend', () => {
        for (item of items) {
          let categories = item.dataset.category.split(',');
          categories = categories.map(it => {
            return it.trim()
          });
          item.hidden = !categories.includes(target);
        }
        accordion.classList.remove('opacity');
      });

    } else {
      for (item of items) {
        let categories = item.dataset.category.split(',');
        categories = categories.map(it => {
          return it.trim()
        });
        item.hidden = !categories.includes(target);
      }
    }
  }
});
$(window).on('load', () => {
  const container = document.querySelector('.__js_timer');

  if (container) {
    const endDate = container.dataset.endDate;
    let countDownDate = new Date(endDate).getTime();
    let daysSpan = document.querySelector('.timer__column--days span');
    let hoursSpan = document.querySelector('.timer__column--hours span');
    let minSpan = document.querySelector('.timer__column--min span');
    let secSpan = document.querySelector('.timer__column--sec span');

    let x = setInterval(function() {
      let now = new Date().getTime();
      let distance = countDownDate - now;
      let days = Math.floor(distance / (1000 * 60 * 60 * 24));
      let hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      let minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
      let seconds = Math.floor((distance % (1000 * 60)) / 1000);

      if (daysSpan) {
        daysSpan.textContent = days.toString().padStart(2, '0');
      }

      if (hoursSpan) {
        hoursSpan.textContent = hours.toString().padStart(2, '0');
      }

      if (minSpan) {
        minSpan.textContent = minutes.toString().padStart(2, '0');
      }

      if (secSpan) {
        secSpan.textContent = seconds.toString().padStart(2, '0');
      }

      if (distance < 0) {
        clearInterval(x);
        container.innerHTML = '<div class="timer__expired">' + container.dataset.expiredText + '</div>';
      }
    }, 1000);
  }
});
